import * as React from 'react';
import user from '../images/user-logo.png'
import eina from '../images/eina-logo.png'
import { Link } from 'react-router-dom'
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DesktopDatePicker from '@mui/lab/DesktopDatePicker';
import TextField from '@mui/material/TextField';
import Avatar from '@mui/material/Avatar';
import Form from 'react-bootstrap/Form'
import FloatingLabel from 'react-bootstrap/FloatingLabel'
import Stack from '@mui/material/Stack'
import axios from 'axios';
import qs from 'qs'

import '../styles/Registro.css';

function Registro() {

  const [fecha, setFecha] = React.useState(new Date());
  const [email, setEmail] = React.useState("");
  const [nombreUsuario, setNombreUsuario] = React.useState("");
  const [nombre, setNombre] = React.useState("");
  const [password1, setPassword1] = React.useState("");
  const [password2, setPassword2] = React.useState("");

  const handleFecha = (newValue) => {
    let date=parseInt(newValue.getMonth()+1) +"-"+ newValue.getDate() +"-"+newValue.getFullYear();
    setFecha(date);
  };

  const handleEmail = (newValue) => {
    setEmail(newValue.target.value);
  };

  const handleNombreUsuario = (newValue) => {
    setNombreUsuario(newValue.target.value);
  };

  const handleNombre = (newValue) => {
    setNombre(newValue.target.value);
  };

  const handlePassword1 = (newValue) => {
    setPassword1(newValue.target.value);
  };

  const handlePassword2 = (newValue) => {
    setPassword2(newValue.target.value);
  };

  const handleRegister = () => {
    if (password1 != password2){
      alert("Las contraseñas no coinciden!!!")
    }else{
      console.log("Proceso a registrar el usuario")
      axios({
        method: 'post',
        url: 'https://musoliber-api.herokuapp.com/addUser',
        data: qs.stringify({
          email: email,
          password: password1,
          nombreUsuario: nombreUsuario,
          fechaNacimiento: fecha,
          nombre: nombre,
          biografia: ""
        })
      }).then(res => {
          alert("Usuario registrado satisfactoriamente")
      }).catch(error => {
        alert("El usuario no ha podido ser registrado")
      });
    }
  }

  return (

    <div className="App">

        <h4 className="titulo">Registrate en Musoliber!</h4>

          <div className="formulario" style={{marginLeft: "25%", width: "50%", height: "50%"}}>
            
            <Form.Floating className="mb-3">
              <Form.Control
                id="floatingInputCustom"
                type="email"
                placeholder="name@example.com"
                value={email}
                onChange={handleEmail}
              />
              <label htmlFor="floatingInputCustom">Dirección de email</label>
            </Form.Floating>

            <Form.Floating className="mb-3">
              <Form.Control
                id="floatingInputCustom"
                type="text"
                placeholder="name@example.com"
                value={nombre}
                onChange={handleNombre}
              />
              <label htmlFor="floatingInputCustom">Nombre</label>
            </Form.Floating>

            <Form.Floating className="mb-3">
              <Form.Control
                id="floatingInputCustom"
                type="text"
                placeholder="name@example.com"
                value={nombreUsuario}
                onChange={handleNombreUsuario}
              />
              <label htmlFor="floatingInputCustom">Nombre de usuario</label>
            </Form.Floating>

            <Form.Floating>
              <Form.Control
                id="floatingPasswordCustom"
                type="password"
                placeholder="Password"
                value={password1}
                onChange={handlePassword1}
              />
              <label htmlFor="floatingPasswordCustom">Contraseña</label>
            </Form.Floating>

            <Form.Floating style={{marginTop: "15px"}}>
              <Form.Control
                id="floatingPasswordCustom-2"
                type="password"
                placeholder="Password"
                value={password2}
                onChange={handlePassword2}
              />
              <label htmlFor="floatingPasswordCustom">Confirmar Contraseña</label>
            </Form.Floating>

            <Stack spacing={3} style={{"margin-top":"25px", "width":"100%"}}>
              <LocalizationProvider dateAdapter={AdapterDateFns}>
                <DesktopDatePicker
                label="Fecha de Nacimiento"
                inputFormat="dd/MM/yyyy"
                value={fecha}
                onChange={handleFecha}
                style={{marginTop: "100px"}}
                renderInput={(params) => <TextField {...params} />}
              />
              </LocalizationProvider>
            </Stack>
            <button style={{marginTop: "20px"}} onClick={handleRegister} type="button" className="btn btn-info btn-lg" > REGISTRARSE </button>
        </div>
      </div>
  );
}

export default Registro;
