import * as React from 'react';
import user from './images/user-logo.png'
import eina from './images/eina-logo.png'
import { Link } from 'react-router-dom'
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DesktopDatePicker from '@mui/lab/DesktopDatePicker';
import TextField from '@mui/material/TextField';
import Avatar from '@mui/material/Avatar';
import Form from 'react-bootstrap/Form'
import FloatingLabel from 'react-bootstrap/FloatingLabel'
import Stack from '@mui/material/Stack';
import './styles/Inicio.css';
import axios from 'axios';
import qs from 'qs'

function Inicio() {

  const [nombreUsuario, setNombreUsuario] = React.useState("");
  const [password, setPassword] = React.useState("");

  const handleNombreUsuario = (newValue) => {
    setNombreUsuario(newValue.target.value);
  };

  const handlePassword = (newValue) => {
    setPassword(newValue.target.value);
  };

  const handleLogin = () => {
    axios({
      method: 'get',
      url: 'https://musoliber-api.herokuapp.com/login',
      data: qs.stringify({
        nombreUsuario: nombreUsuario,
        password: password
      }),
      responseType: Boolean
    }).then(res => {
      if (res) {
        // Como redireccionar a la pagina principal
      } else {
        alert("Nombre de usuario o contraseña incorrectos")
      }
    }).catch(error => {
      alert("Error en el login. Contacte con nosotros")
    });
  }

  return (
    <div className="App">
      <div className="fotoUsuario">
        <Avatar alt="Remy Sharp" src={user} style={{width: "100px", height: "100px"}} />
        </div>

        <div style={{marginLeft: "25%", width: "50%", height: "50%"}}>
        <>

        <Form.Floating style={{marginLeft: "25%", marginTop: "10px", width: "50%"}} className="mb-3">
          <Form.Control
            id="floatingInputCustom"
            type="text"
            placeholder="name@example.com"
            value={nombreUsuario}
            onChange={handleNombreUsuario}
          />
          <label htmlFor="floatingInputCustom">Nombre de usuario</label>
        </Form.Floating>

        <Form.Floating style={{marginLeft: "25%", marginTop: "15px", width: "50%", marginBottom: "15px"}}>
          <Form.Control
            id="floatingPasswordCustom"
            type="password"
            placeholder="Password"
            value={password}
            onChange={handlePassword}
          />
          <label htmlFor="floatingPasswordCustom">Contraseña</label>
        </Form.Floating>

          <h10> ¿No te acuerdas de la contraseña?
          <Link to="/recuperar-contraseña"><h10 className="contra-olvidada"> Te ayudamos! </h10> </Link>
          </h10> <br></br>

          <button style={{marginTop: "20px"}} onClick={handleLogin} type="button" className="btn btn-info btn-lg" > INICIAR SESIÓN </button>
          <Link to="/registro"><button style={{marginTop: "22px", marginRight:"15px"}} type="button" class="btn btn-outline-info btn-lg" > REGISTRARME </button></Link>
        </>
      </div>
    </div>
  );
}

export default Inicio;
